import React, { useEffect, useState } from 'react'
import { Container,Row,Col,Card } from 'react-bootstrap'
import AdminHeader from '../AdminHeader/AdminHeader'
import AdminSideMenu from '../SideMenu/AdminSideMenu'
import axios from 'axios'

export default function UserDetails() {

  const [data,setData]=useState([])
   
  useEffect(()=>{
      getUser()
    },[])
  
  const getUser= async()=>{
    const response=await axios.get('/user/getusers');
    if(response.status===200){
      setData(response.data)
    }
  }
  console.log("data===",data)

  return (
    <div>
      <div>
          <AdminHeader/>
        </div>  
        <div>
        <Container className="AdminPage">
                <Row className="my-1">
                    <Col lg={3}>
                        <AdminSideMenu/>
                    </Col>
                    <Col lg={9}>
                    <Card className="p-4">
                            <h4>View Booking View</h4>
                            <div className=''>
                                <table>
                                    <thead>
                                        <th>Department</th>
                                        <th>Reason</th>
                                        <th>Staff Name</th>
                                        <th>Date</th>
                                        <th>Starting Time</th>
                                        <th>Ending Time</th>
                                        <th>Edit/Delete</th>

                                    </thead>
                                    <tbody className='p-3'>
                                        
                                            {data.map(value =>
                                                <tr >
                                                    <td className='p-3'>{value.department}</td>
                                                    <td className='p-3'>{value.name}</td>
                                                    <td className='p-3'>{value.email}</td>

                                                    
                                                </tr>
                                            )}
                                       
                                    </tbody>
                                </table>
                            </div>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </div>

    </div>
  )
}
